/* search best place */
.container-bestplace {
    margin: 152px 137px 70px 133px;
}

.container-bestplace .center {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.container-bestplace .titles .title {
    font-size: 48px;
    font-weight: 600;
    color: #23262F;
}

.container-bestplace .titles .content{
    width: 593px;
    Height: 48px;
    font-size:16px;
    font-weight: 400;
    color: #84878B;
    margin-top: 12px;
}

.container-bestplace .items {
    width: 100%;
    height: 434px;
    margin-top: 65px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 30px;
}

.container-bestplace .items .item {
    /* width: 270px;
    height: 202px; */
    width: 100%;
    height: 100%;
    border: 1px solid #E7ECF3;
    box-sizing: border-box;
    border-radius: 10px;
    box-shadow: 0px 20px 51px rgba(96, 96, 96, 0.1);
    background: #FFFFFF;
}

.container-bestplace .items .item .image {
    display: flex;
    justify-content: space-between;
}

.container-bestplace .items .hot {
    position: relative;
    width: 53px;
    height: 14px;
    background-color: #EE6D52;
    font-size: 12px;
    line-height: 14px;
    font-feature-settings: 'kern' off;
    color: #FFFFFF;
    margin-right: 38px;
    display: flex;
    justify-content: center;
}

.container-bestplace .items .hot::before {
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    top: 2px;
    left: -12px;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-bottom: 10px solid #EE6D52;
    transform: rotate(-90deg);
}

.container-bestplace .items .hot::after {
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    top: 2px;
    right: -12px;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-top: 10px solid #EE6D52;
    transform: rotate(-90deg);
}

.container-bestplace .items .item .image {
    margin-top: 28px;
    margin-left: 30px;
}

.container-bestplace .items .item .image-item{
    width: 74px;
    height: 74px;
    border-radius: 10px;
}

.container-bestplace .items .item .information {
    margin-top: 16px;
    margin-left: 30px;
}

.container-bestplace .items .item .information .name-info{
    font-weight: 600;
    font-size: 20px;
    line-height: 26px;
    color: #3B3E44;
}

.container-bestplace .items .item .information .address-info{
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #84878B;
    margin-top: 7px;
}

