/* travel */
.travel-contain {
    margin: 70px 135px 120px 135px;
}

.travel-contain h2 {
    font-weight: 600;
    font-size: 48px;
    line-height: 70px;
    letter-spacing: -0.005em;
    color: #141416;
    margin-bottom: 12px;
}

.travel-contain p {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #84878B;
    margin-bottom: 42px;
}

.travel-contain .travel-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    grid-gap: 30px;
}

.travel-contain .travel-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    grid-gap: 30px;
    width: 100%;
    height: auto;
}

.travel-contain .travel-grid .grid {
    position: relative;
    overflow: hidden;
    border-radius: 15px;
}

.travel-contain .travel-grid .grid .card-img {
    position: relative;
    height: 100%;
    width: 100%;
    background: linear-gradient(1.52deg, rgba(22, 22, 22, 0.48) 14.56%, rgba(35, 35, 35, 0) 25.42%);
}

.travel-contain .travel-grid .grid .card-img img{
    width: 100%;
    height: 100%;
    background-size: contain;
    object-fit: cover;
}

.travel-contain .travel-grid .grid:nth-child(1) {
    grid-column: 1/2;
    grid-row: 1/4;
}

.travel-contain .travel-grid .grid:nth-child(2) {
    grid-column: 2/3;
    grid-row: 1/4;
}

.travel-contain .travel-grid .grid:nth-child(3) {
    grid-row: 1/4;
}

.travel-contain .travel-grid .grid:nth-child(4) {
    grid-column: 4/5;
    grid-row: 1/4;
}

.travel-contain .travel-grid .grid .content {
    position: absolute;
    bottom: 17px;
    left: 30px;
}

.travel-contain .travel-grid .grid .content .title {
    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
    color: #FFFFFF;
}

.travel-contain .travel-grid .grid .content .star {
    font-size: 14px;
    line-height: 21px;
    color: #FFFFFF;
    display: flex;
    align-items: center;
    margin-top: 5px;
}

.travel-contain .travel-grid .grid .content .star .icon-star{
    font-size: 18px;
    color: #FFD166;
    margin-right: 10px;
}

.travel-contain .travel-grid .grid .content .star .text{
    font-weight: 200;
    font-size: 14px;
    line-height: 21px;
    color: #FFFFFF;
}

@media only screen and (max-width: 375px) {
