@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&family=Rubik:wght@300;400;500;600;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}

.container-nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 78px;
    font-size: 14px;
    background: #FFFFFF;
}

.container-nav .style-logo {
    display: flex;
    align-items: center;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    color: #222529;
    position: relative;
    left: -28px;
}

.container-nav .style-logo p {
    position: relative;
    left: -15px;
}

.container-nav .logo {
    width: 100px;
    margin-left: 40px;
}

.container-nav .usa {
    width: 20px;
    height: 20px;
    object-fit: cover;
    border-radius: 50%;
}

.container-nav .avatar {
    width: 32px;
    height: 32px;
    object-fit: cover;
    border-radius: 50%;
}

.container-nav .nav-menu {
    font-weight: 500;
}

.container-nav .nav-menu p{
    color: #84878B;
}

.container-nav ul {
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 5px;
    margin-right: 40px;
}

.container-nav ul:last-child {
    margin-right: 0;
}

.container-nav ul li {
    margin-right: 17px;
}

.container-nav .icon-button {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #FFFFFF;
    width: 17px;
    height: 17px;
    border: none;
    outline: none;
    cursor: pointer;
}

.container-nav .icon-button_drop {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #FFFFFF;
    border: none;
    outline: none;
    cursor: pointer;
}

.container-nav .icon-button__badge {
    position: absolute;
    background: #EB5757;
    color: #FFFFFF;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    top: -1px;
    right: -2px;
    width: 9px;
    height: 9px;
    font-size: 6px;
}

@media only screen and (max-width: 375px) { 
    .container-nav ul li {
        margin-right: 15px;
    }
    
    .container-nav .style-logo {
        display: flex;
        align-items: center;
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        line-height: 18px;
        color: #222529;
        position: relative;
        left: -50px;
    }

    .container-nav .logo {
        width: 80px;
        margin-left: 40px;
    }

    .nav-menu li:nth-child(6),
    .nav-menu li:nth-child(7) {
        display: none;
    }
}