@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&family=Rubik:wght@300;400;500;600;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}

.footer {
    margin: 100px 190px 152px 190px;
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1fr;
}

.footer .container .content-logo .detail {
    width: 254px;
    height: 72px;
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 24px;
    letter-spacing: 0em;
    color: #84878B;
    margin-bottom: 24px;
    text-align: left; 
}

.footer .container .light .sun {
    font-size: 24px;
    margin: 9px 24px;
    color: #3B3E44;
}

.footer .container .light .moon {
    font-size: 24px;
    margin: 9px 24px;
    color: #868B90;
}

.footer .container .content-logo .content {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
}

.footer .container .content-logo .content .title-logo {
    font-size: 20px;
    font-weight: 600;
    line-height: 27px;
    letter-spacing: 0em;
    text-align: left;
    position: relative;
    left: -45px;
}

.footer .container .content-logo img {
    width: 100px;
    position: relative;
    left: -30px;
}

.footer .box-content .title {
    font-size: 24px;
    font-weight: 500;
    line-height: 36px;
    letter-spacing: 0em;
    color: #222529;
    text-align: left; 
}

.footer .box-content p {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left; 
    color: #84878B;
    margin: 16px 0 16px 60px;
}

.footer .box-content p:nth-child(2) {
    margin-top: 18px;
}

@media only screen and (max-width: 375px) { 
    .footer {
        margin: 21px 30px 30px 30px;
        grid-template-columns: unset;
        grid-template-rows: 1.5fr 1.5fr 1.5fr 0.5fr;
    }

    .footer .container {
        grid-row: 4/5;
    }

    .footer .container .content-logo .content {
        display: flex;
        align-items: center;
        margin-bottom: 5px;
    }
    
    .footer .container .content-logo .content .title-logo {
        font-size: 20px;
        font-weight: 600;
        line-height: 27px;
        letter-spacing: 0em;
        text-align: left;
        position: relative;
        left: -45px;
    }
    
    .footer .container .content-logo img {
        width: 100px;
        position: relative;
        left: -30px;
    }
    
    .footer .box-content .title {
        font-size: 18px;
        font-weight: 500;
        line-height: 27px;
        color: #222529;
        text-align: left; 
    }

    .footer .box-content {
        border-bottom: 1px solid #E7ECF3;
        padding-bottom: 21px;
        margin-bottom: 15px;
    }

    .footer .box-content:last-child {
        border-bottom: none;
        padding-bottom: 0;
        margin-bottom: 0;
    }
    
    .footer .box-content p {
        font-size: 12px;
        font-weight: 500;
        line-height: 18px;
        text-align: left; 
        color:#3B3E44;
        margin: 5px 0;
    }

    .footer .box-content p:nth-child(2) {
        margin-top: 10px;
    }

    .footer .container .light .sun {
        font-size: 24px;
        margin: 9px 24px;
        color: #3B3E44;
    }
    
    .footer .container .light .moon {
        font-size: 24px;
        margin: 9px 20px;
        color: #868B90;
    }
    
    .footer .container .content-logo .content {
        display: flex;
        align-items: center;
        margin-bottom: 5px;
    }
    
    .footer .container .content-logo .content .title-logo {
        font-size: 14px;
        font-weight: 600;
        line-height: 14px;
        letter-spacing: 0em;
        text-align: left;
        position: relative;
        left: -22px;
    }
    
    .footer .container .content-logo img {
        width: 60px;
        position: relative;
        left: -15px;
    }

    .footer .container .content-logo .detail {
        display: none;
    }

    .footer .container .light {
        margin-top: 34px;
        margin-bottom: 15px;
    }
}
